* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.style-btn {
  width: 120px;
  height: 40px;
  color: #30322f;
  cursor: pointer;
  background-color: #efd6c1;
  border: none;
  border-radius: 15px;
  outline: none;
}

.style-btn:hover, .style-btn:focus {
  color: #e22e2e;
  background-color: #7fffd4;
}

.timer {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-size: 50px;
  font-weight: 700;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
  display: flex;
}

.label {
  font-size: 25px;
}

.color-btn {
  color: #142003;
  cursor: pointer;
  width: 85px;
  height: 30px;
  background-color: #139090;
  border: none;
  border-radius: 10px;
  margin-left: 10px;
}

.color-btn:hover, .color-btn:focus {
  color: #082965;
  background-color: #44d8d8;
}

.input-style {
  width: 230px;
  height: 30px;
  border: #0000;
  border-radius: 20px;
  padding: 10px 20px;
}

.input-style:hover, .input-style:focus {
  color: #00f;
  outline-color: #00f;
}

.value {
  color: #333;
  animation: 1s linear infinite value-animation;
}

@keyframes value-animation {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

.form {
  border: 2px solid #ccc;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2rem;
  padding: 2rem;
  font-size: 1.2rem;
  display: flex;
  box-shadow: 0 0 5px #0000001a;
}

label {
  flex-direction: column;
  margin-bottom: 1rem;
  display: flex;
}

input {
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: .5rem;
  font-size: 1.2rem;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: green;
  border: none;
  border-radius: 3px;
  padding: .5rem 1rem;
  font-size: 1.2rem;
}

button:hover {
  background-color: #006400;
}

button:active {
  background-color: #040;
  box-shadow: inset 0 1px 3px #00000080;
}

.promise-animation {
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
  font-size: 2rem;
  animation: 1s linear infinite value-animation;
  display: flex;
}

